@mixin section_name {
  width: 112px;
  height: 24px;
  object-fit: contain;
  background-color: #000000;
  font-family: Roboto;
  font-size: 11px;
  font-weight: 100;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
  margin: 0 auto;
  line-height: 24px; }

@mixin header {
  object-fit: contain;
  font-family: Roboto;
  font-size: 38px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 48px;
  letter-spacing: normal;
  text-align: center;
  color: #222222;
  margin-top: 38px; }

@mixin btn_hover {
  &:hover {
    background-image: none;
    background-color: #1eacc7; } }
