@media (max-width: 1200px) {
  .menu {
    .menu_item {
      padding-right: 20px; }
    .menu_link {
      font-size: 13px; } }

  .promo {
    .promo_header {
      font-size: 50px; } }

  .advantages {
    .adv_item {
      align-items: flex-start;
      .adv_text {
        width: 300px; } } }

  .mobile {}
		.mob_item {
			width: 290px;
			.mob_subtitle {
				font-size: 22px; } }

  .choice {}
		padding: 55px 0;
		min-height: 840px; }



@media (max-width: 991px) {
  .menu {
    .menu_item {
      padding-right: 8px; }
    .menu_link {
      font-size: 10px; } }

  .subheader {
    .subheader_logo img {
      width: 100%; }
    .subheader_official {
      font-size: 11px; }
    .subheader_phone {
      font-size: 18px; }
    .subheader_btn {
      margin-top: 21px;
      width: 100%;
      height: 40px; } }

  .promo {
    .promo_header {
      font-size: 40px; }
    .promo_subheader {
      font-size: 30px; } }

  .advantages {
    .adv_item {
      flex-direction: column;
      align-items: center;
      .adv_round {
        margin-right: 0; }
      .adv_text {
        width: 300px;
        margin-top: 20px; } } }

  .cities {
		  min-height: 650px; }

  .choice {
    .choice_img {
      width: 100%; } }

  footer {
    &::before {
      content: '';
      display: block;
      width: 280px;
      height: 4px;
      object-fit: contain;
      background-color: #1fbad6;
      margin: 0 auto; }

    .main_links {
      li {
        a.link {
          font-size: 10px; } } }

    .extra_links {
      li {
        a {
          font-size: 9px; } } } } }

@media (max-width: 767px) {
  .menu {
    position: fixed;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: 0;
    left: -100%;
    width: 50%;
    height: 100vh;
    padding: 20px 15px;
    background-color: rgba(0, 0, 0, .8);
    transition: 0.6s;
    &_active {
      left: 0;
      .menu_item {
        padding-right: 0;
        border: none;
        margin-top: 10px; }
      .menu_link {
        font-size: 20px; }
      z-index: 100; } }
  nav {
    .hamburger {
      display: block;
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translateY(-50%);
      width: 26px;
      height: 12px;
      span {
        display: block;
        height: 2px;
        width: 100%;
        background-color: #fff;
        margin-bottom: 2px;
        transition: 0.5s; }
      &_active {
        span {
          margin-bottom: -2px;
          &:nth-child(1) {
            transform: translateY(3px) rotate(-45deg); }
          &:nth-child(2) {
            display: none; }
          &:nth-child(3) {
            transform: translateY(3px) rotate(45deg); } } } } }

  .xs-hidden {
    display: none; }
  .xs-visible {
    display: block; }

  .subheader {
    .subheader_phone {
      display: block;
      margin-top: 10px;
      font-family: Roboto;
      font-size: 16px;
      font-weight: 400;
      font-stretch: normal;
      font-style: normal;
      letter-spacing: normal;
      text-align: center;
      color: #ffffff; }

    .subheader_btn {
      display: block;
      width: 200px;
      height: 40px;
      margin: 0 auto;
      margin-top: 10px; } }

  .promo {
    .promo_header {
      line-height: 45px; } }

  footer {
    height: 155px;
    .soc_nets {
      bottom: 0px; }
    .stores {
      bottom: 0px; }
    .main_links {
      li {
        a.link {
          font-size: 15px; } } }
    .extra_links {
      li {
        a {
          font-size: 12px; } } } } }

@media (max-width: 575px) {
  .subheader {
    height: 60px;
    .subheader_official {
      display: none; }

    .subheader_logo {
      margin-top: 16px; }

    .subheader_phone {
      font-family: Roboto;
      font-size: 12px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      letter-spacing: normal;
      text-align: center;
      color: #ffffff;
      margin-top: 11px; }

    .subheader_btn {
      width: 110px;
      height: 22px;
      font-family: Roboto;
      font-size: 10px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      letter-spacing: normal;
      text-align: center;
      color: #ffffff;
      margin-top: 6px; } }

  .promo {
    min-height: 400px;
    padding: 113px 0 35px 0;
    .promo_descr {
      display: none; }
    .promo_header {
      padding: 0 24px;
      font-size: 30px;
      line-height: 35px; }
    .promo_subheader {
      padding: 0 10px;
      font-size: 20px;
      margin-top: 33px;
      line-height: 30px; }
    .promo_btn {
      width: 210px;
      height: 50px;
      margin: 0 auto;
      margin-top: 72px;
      font-family: Roboto;
      font-size: 16px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      letter-spacing: normal;
      text-align: center;
      color: #ffffff; } }
  .menu {
    width: 85%; }

  .advantages {
    padding: 22px 0 50px 0;

    .adv_item {
      .adv_round {
        width: 100px;
        height: 100px; }
      .adv_text {
        width: 100%;
        margin-top: 27px; } } }

  .title {
    font-size: 16px;
    margin-top: 15px;
    line-height: 16px; }

  .subtitle {
    font-size: 14px;
    margin-top: 11px;
    line-height: 21px; }

  .mobile {
    padding-top: 30px;
    padding-bottom: 43px;

    .mob_item {
      width: 100%;
      height: 254px;
      padding: 81px 43px 28px 43px;
      .mob_item_subtitle {
        object-fit: contain;
        font-family: Roboto;
        font-size: 18px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff; } } }

  .choice {
    padding: 25px 0 33px 0;
    min-height: 390px;
    background: none;
    background-color: #070716;
    .subtitle {
      display: none; }
    .title {
      display: none; }
    .choice_img {
      margin-top: 35px; }
    .choice_descr {
      font-family: Roboto;
      font-size: 17px;
      font-weight: 300;
      font-stretch: normal;
      font-style: normal;
      line-height: 24px;
      letter-spacing: normal;
      text-align: center;
      color: #ffffff; }
    .black {
      display: none; } }

  footer {
    min-height: 245px;
    padding: 24px 0 9px 0;
    &::before {
      content: '';
      display: block;
      width: 253px;
      height: 4px;
      object-fit: contain;
      background-color: #1fbad6;
      margin: 0 auto; }

    .main_links {
      margin-top: 19px;
      li {
        a.link {
          font-size: 14px; } } }
    .extra_links {
      margin-top: 11px;
      li {
        a {
          font-size: 9px; } } }
    .language {
      margin-top: 12px; }
    .soc_nets {
      bottom: 53px;
      left: 50%;
      transform: translateX(-50%); }
    .stores {
      bottom: 9px;
      left: 50%;
      transform: translateX(-50%); } } }
