.mobile {
  min-height: 864px;
  padding-top: 48px;
  padding-bottom: 80px;
  background: url('../img/bg/bg_white.jpg') center (center / cover) no-repeat;

  .row {
    margin-top: 32px; }

  .mob_name {
    @include section_name;
    margin-top: 0; }

  .mob_header {
    @include header; }

  .mob_subheader {
    object-fit: contain;
    font-family: Roboto;
    font-size: 17px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: normal;
    text-align: center;
    color: #222222;
    margin-top: 19px; }

  .mob_item {
    width: 320px;
    height: 280px;
    margin: 0 auto;
    padding-top: 92px;
    margin-bottom: 19px;
    position: relative;

    &_1 {
      background: url('../img/mobile_app/img1.png') center (center / cover) no-repeat; }

    &_2 {
      background: url('../img/mobile_app/img2.png') center (center / cover) no-repeat; }

    &_3 {
      background: url('../img/mobile_app/img3.png') center (center / cover) no-repeat; }

    &_4 {
      background: url('../img/mobile_app/img4.png') center (center / cover) no-repeat; }

    &_5 {
      background: url('../img/mobile_app/img5.png') center (center / cover) no-repeat; }

    &_6 {
      background: url('../img/mobile_app/img6.png') center (center / cover) no-repeat; }

    .mob_item_subtitle {
      object-fit: contain;
      font-family: Roboto;
      font-size: 24px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      letter-spacing: normal;
      text-align: center;
      color: #ffffff; }

    &_plus {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      bottom: 35px;
      left: 50%;
      transform: translateX(-50%);
      width: 26px;
      height: 26px;
      object-fit: contain;
      background-color: #000000;
      border-radius: 100%;
      margin: 0 auto;
      cursor: pointer;
      &:before {
        content: '';
        display: block;
        width: 14px;
        height: 2px;
        background-color: #1aecc7;
        transition: 0.2s; }
      &:after {
        content: '';
        display: block;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        width: 2px;
        height: 14px;
        background-color: #1aecc7;
        transition: 0.2s; }
      &:hover {
        &:before, &:after {
          background-color: #ff5656; } } } } }

